*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.navbar {
    background:rgba(0, 0, 0, .65) url('https://jobhunt.madrasthemes.com/wp-content/uploads/2018/09/home-bg.jpg');
    background-blend-mode: darken;
    height: 800px;
    display: flex;
    justify-content: space-around;
    color :white;
    padding:20px;
    padding-top: 20px;
  } 
  button {
    margin-top:50px;
    padding: 15px;
    height:45px;
    cursor: pointer;
    border-radius: .5rem;
    border: none;
    color: white;
    background-color: #48cae4;
    font-size: 1.1rem;
    letter-spacing: 0.1rem;
    text-transform: uppercase;
    transition: 0.3s ease-in-out;
  }
    button:hover {
      background-color: #023e8a;
    }
.logo {
    width: 50px;
    height: 50px;
    }
    .logocolor{
        background-color:brown;
    }

  .nav-links{
    display: flex;
    justify-content: flex-end;
    list-style: none;
    width: 75%;
  }
  .home,
  .skills,
  .about,
  .contact{
    text-decoration: none;
    color: white;
    font-size: 20px;
    padding: 10px 20px;
    margin: 0 10px;
    cursor: pointer;
}


    
.mobile-menu-icon{
    display: none;
    
}

@media screen and (max-width: 780px){

    .logo {
        display: flex;
        position: absolute;
        top: 15px;
        left: 35px;
    }
    .nav-links{
        display:none;

    }
    .nav-links-mobile{
        position: absolute;
        display: block;
        list-style: none;
        background-color:#005569;
        left: 0;
        top: 20%;
        transition: all 0.5s ease-out;
        width: 100%;
    }
    .home,
    .skills,
    .about,
    .contact
    {
        color: white;
        text-align: center;
        padding: 32px;
        width: 100%;
        transition: all 0.5s ease;
    }
    .contact{
        border: none;
    }
    .mobile-menu-icon{
        display: block;
        position: absolute;
        font-size: 30px;
        color:white;
        background-color:#00828c;
        border: none; 
        outline: none;
        top: 15px;
        right: 25px;
    }

}
  
  